body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Header { 
    width: 100%; 
    height: 75px;

    text-align: left;  

    margin: 0;

    background-color: azure;
}

h1 { 
    margin: 0;
    padding-left: 15px;
}
.line { 
    position: absolute;
    stroke: black;
    stroke-width: 1; 
    padding-top: 5px;
    z-index: -1;
}

.chordEntry { 
    width: 10px;
}
 
tr { display: block; float: left; }
th, td { display: block; }

td input:hover { 
    padding: 0px;
    outline: #333 solid 1px;
}
.tuner_input { 
    width: 5%;
}
.container { 
  margin-left: 20px;
  width: 95%;
}
 
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
  
button, select, input { 
  margin-left: 5px;
}
